<script>
export default {
  data() {
    return {
      hoverPoints: false,
      blured: true,
      elArr: [],
      elArr2: [],
      pathArr: [],
      pathArr2: [],
      popUpArr: [],
      popUpArr2: [],
      pointArr: [],
      pointArr2: [],
      slide1: false,
      slide2: false,
    };
  },
  mounted() {
    this.elArr.push(this.$refs.el_1, this.$refs.el_3, this.$refs.el_5);
    this.pathArr.push(this.$refs.path_1, this.$refs.path_3, this.$refs.path_5);
    this.popUpArr.push(
      this.$refs.pop_up_1,
      this.$refs.pop_up_3,
      this.$refs.pop_up_5
    );
    this.pointArr.push(
      this.$refs.point_1,
      this.$refs.point_3,
      this.$refs.point_5
    );

    this.elArr2.push(this.$refs.el_2, this.$refs.el_4);
    this.pathArr2.push(this.$refs.path_2, this.$refs.path_4);
    this.popUpArr2.push(this.$refs.pop_up_2, this.$refs.pop_up_4);
    this.pointArr2.push(this.$refs.point_2, this.$refs.point_4);
    this.slideShow();
  },

  created() {
    window.addEventListener("resize", this.showPopUp);
    window.addEventListener("resize", this.strartSlide1);
  },
  name: "infographic",
  props: { scrollY: Number, clientWidth: Number },
  components: {},
  computed: {},
  watch: {
    slide1(newValue) {
      if (newValue && this.clientWidth>670) {
        setTimeout(this.strartSlide2, 7500);
      } else {
        setTimeout(this.strartSlide1, 7500);
      }
    },
  },
  methods: {
    showPopUp(el, ref, path, hover, active) {
      if (active) {
        const domRectEl = el.getBoundingClientRect();
        const domRectRef = ref.getBoundingClientRect();
        const height = domRectEl.bottom - domRectRef.height - 15;
        ref.classList.add("show");
        path.classList.add("show");
        el.classList.add("show");
        ref.style.top = height + "px";
        ref.style.left = domRectEl.left + "px";
      }
    },
    slideShow() {
      setTimeout(this.strartSlide1, 900);
      this.blured = false;
    },
    getClientsValue() {},
    strartSlide1() {
      //   removing previous slide values
      this.slide1 = true;
      this.slide2 = false;
      this.elArr2.forEach((i) => {
        const num = this.elArr2.indexOf(i);
        i.classList.remove("show");
        this.popUpArr2[num].classList.remove("show");
        this.pathArr2[num].classList.remove("show");
        this.pointArr2[num].classList.remove("show");
      }),
        this.elArr.forEach((i) => {
          //    getting client position values
          const num = this.elArr.indexOf(i);
          const domRectEl = i.getBoundingClientRect();
          const domRectRef = this.popUpArr[num].getBoundingClientRect();
          const height = domRectRef.height + 10;
          const x = domRectEl.x;
          const y = domRectEl.y + this.scrollY - height;
          this.popUpArr[num].style.transform = `translate(${x}px,${y}px)`;
          //   adding class 'show'
          this.popUpArr[num].classList.add("show");
          i.classList.add("show");
          this.pathArr[num].classList.add("show");
          this.pointArr[num].classList.add("show");
        });
    },
    strartSlide2() {
      //   removing previous slide values
      this.slide1 = false;
      this.slide2 = true;
      this.elArr.forEach((i) => {
        const num = this.elArr.indexOf(i);
        i.classList.remove("show");
        this.popUpArr[num].classList.remove("show");
        this.pathArr[num].classList.remove("show");
        this.pointArr[num].classList.remove("show");
      }),
        this.elArr2.forEach((i) => {
          //    getting client position values
          const num = this.elArr2.indexOf(i);
          const domRectEl = i.getBoundingClientRect();
          const domRectRef = this.popUpArr2[num].getBoundingClientRect();
          const height = domRectRef.height + 10;
          const x = domRectEl.x;
          const y = domRectEl.y + this.scrollY - height;
          this.popUpArr2[num].style.transform = `translate(${x}px,${y}px)`;
          //   adding class 'show'
          this.popUpArr2[num].classList.add("show");
          i.classList.add("show");
          this.pathArr2[num].classList.add("show");
          this.pointArr2[num].classList.add("show");
        });
    },
  },
};
</script>
