<template>
  <div>
    <ul class="collection" id="header_wrp">
      <li class="collection-item row" id="header_block">
        <leadcheck @inputData="check" />
        <span>№</span>
        <span>статус</span>
        <span>клиент</span>
        <span>
          <i class="material-icons center">mail_outline</i>
        </span>
        <span>телефон</span>

        <span :class="!sort && !settings ? 'is_active' : 'is_hidden'"
          >когда перезвонить</span
        >
        <div class="white_space">
          <!-- filter  -->
          <div
            class="filter_wrp"
            :class="!sort ? 'normal' : 'is_moved'"
            ref="filter_wrp"
          >
            <i
              v-if="!sort && !settings"
              class="material-icons"
              @click="openFilter"
              ref="tooltippedFilter"
            >
              {{ icon }}
            </i>
            <span v-if="sort" @click="dropDown">
              <i class="material-icons">sort</i>
              фильтр: {{ checkedFilter }}
              <i v-if="dropdown" class="material-icons">arrow_drop_up</i>
              <i v-else class="material-icons">arrow_drop_down</i></span
            >
            <i
              v-if="sort && !settings"
              class="material-icons close"
              @click="openFilter"
              ref="tooltippedFilter"
            >
              {{ icon }}
            </i>
          </div>
          <!-- settings -->
          <div
            class="settings_wrp"
            :class="!settings ? 'normal' : 'is_moved'"
            ref="settings_wrp"
          >
            <i
              v-if="!settings && !sort"
              class="material-icons"
              @click="openSettings"
              ref="tooltippedSettings"
            >
              settings
              <!-- {{ icon }} -->
            </i>
            <span v-if="settings" @click="dropDown">
              <i class="material-icons">settings</i>
              настройки
              <i v-if="dropdown" class="material-icons">arrow_drop_up</i>
              <i v-else class="material-icons">arrow_drop_down</i></span
            >
            <i
              v-if="settings"
              class="material-icons close"
              @click="openSettings"
              ref="tooltippedFilter"
            >
              close
              <!-- {{ icon }} -->
            </i>
          </div>
        </div>
      </li>
    </ul>
    <!-- filter dropdown -->
    <ul
      :class="dropdown ? 'is_active' : 'is_hidden'"
      class="filter_menu"
      v-if="sort"
    >
      <li
        class="filter_link"
        :class="checkedFilter == status ? 'active' : ''"
        v-for="(status, i) in status"
        :key="i"
        @click="filter(i)"
      >
        {{ status }}
      </li>
    </ul>
    <!-- settings dropdown -->
    <ul class="filter_settings" v-if="settings" :class="dropdown ? 'is_active' : 'is_hidden'">
      <li class="settings_link">
        <leadcheck id="link_input" @inputData="showDeletedLeads" />
        отображать удалённые
      </li>
      <li class="settings_link">
        <leadcheck id="link_input" @inputData="showDeletedLeads" />
        отображать изменения
      </li>
      <li class="settings_link">
        <leadcheck id="link_input" @inputData="showDeletedLeads" />
        отображать сообщения
      </li>
      <li class="settings_link">
        <leadcheck id="link_input" @inputData="showDeletedLeads" />
        отображать дату создания
      </li>
    </ul>
  </div>
</template>

<script>
import M from "materialize-css";
import leadcheck from "./leadcheck.vue";
export default {
  name: "leadheader",
  data() {
    return {
      checkedFilter: `${this.status[3]}`,
      icon: "sort",
      dropdown: false,
      sort: false,
      settings: false,
      allChecked: false,
      onClick: false,
      helperText: "сортировать",
    };
  },
  mounted() {
    this.toolTipFilter = M.Tooltip.init(this.$refs.tooltippedFilter, {
      position: "right",
      html: this.helperText,
      exitDelay: 100,
    });
    this.toolTipSettings = M.Tooltip.init(this.$refs.tooltippedSettings, {
      position: "right",
      html: "настройки",
      exitDelay: 100,
    });
  },
  components: {
    leadcheck,
  },
  watch: {
    allChecked(newValue) {
      this.$emit("allChecked", newValue);
    },
    sort(newValue) {
      if (newValue) {
        this.dropdown = false;
        this.icon = "close";
        M.Tooltip.init(this.$refs.tooltippedFilter, {
          position: "right",
          html: "закрыть",
          exitDelay: 100,
        });
      } else {
        this.icon = "sort";
        M.Tooltip.init(this.$refs.tooltippedFilter, {
          position: "right",
          html: "cортировать",
          exitDelay: 100,
        });
      }
    },
  },
  props: {
    status: Object,
  },
  computed: {},
  methods: {
    check() {
      this.allChecked = !this.allChecked;
    },
    openFilter() {
      this.sort = !this.sort;
      this.toolTipFilter.close();
    },
    openSettings() {
      this.settings = !this.settings;
      this.toolTipSettings.close();
    },
    dropDown() {
      this.dropdown = !this.dropdown;
    },
    async filter(status) {
      if (status != 3) {
        const data = await this.$axios.post("lead/filter", { status: status });
        const filteredData = data.data;
        console.log(filteredData);
        this.$emit("filter", filteredData);
      } else {
        this.$emit("getAll");
      }
      this.checkedFilter = this.status[status];
      this.dropdown = false;
    },
    showDeletedLeads() {},
  },
};
</script>
